
import $ from 'jquery'
export default {
    getRegions: function (jsonData) {
        var uniqueRegions = [];
        var regions = [];
        $(jsonData).each(function () {
            regions.push(this.winner_region)
        });
        uniqueRegions = regions.filter(function (item, pos) {
            return regions.indexOf(item) == pos;
        })
        return uniqueRegions;
    },
    getCategories: function (jsonData) {
        var uniqueCategories = [];
        var categories = [];
        $(jsonData).each(function () {
            categories.push(this.award_cat_title)
        });
        console.log(categories);
        uniqueCategories = categories.filter(function (item, pos) {
            return categories.indexOf(item) == pos;
        })
        return uniqueCategories;
    },

}
